import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import BackgroundImage from "../components/BackgroundImage"
import SEO from "../components/seo"

import styled from "styled-components"

const Row = styled.div`
  height: 800px;
  width: 100%;
  display: flex;
`
const ImageClipper = styled.div`
  height: 100%;
  width: 100%;
  position: absolute;
  clip: rect(auto, auto, auto, auto);
`

const RowBlock = styled.div`
  height: 100%;
  width: 50%;
  background: rgba(0, 200, 0, 0.5);
  position: relative;

  overflow: hidden;

  :nth-of-type(2) {
    background: rgba(0, 100, 0, 0.5);
  }
`

const RowBlockTextContainer = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  align-self: center;
  align-content: center;
  justify-content: center;
  height: 100%;
  overflow: hidden;

  padding: 2em 5em;
  text-align: justify;

  &.light {
    color: white;
  }
`

const IndexPage = () => (
  <Layout fullWidth>
    <SEO title="Home" />

    <Row>
      <RowBlock>
        <RowBlockTextContainer>
          <h1>Text</h1>
          <p>
            Pug actually next level poutine, post-ironic leggings farm-to-table
            microdosing four dollar toast kale chips trust fund gluten-free
            irony direct trade. Next level synth try-hard celiac cliche, you
            probably haven't heard of them heirloom butcher.
          </p>
        </RowBlockTextContainer>
      </RowBlock>
      <RowBlock>
        <ImageClipper className="ImageClipper">
          <BackgroundImage name="green_soap_hd.png"></BackgroundImage>
        </ImageClipper>
      </RowBlock>
    </Row>
    <Row>
      <RowBlock>
        <ImageClipper className="ImageClipper">
          <BackgroundImage name="black_soap_hd.png"></BackgroundImage>
        </ImageClipper>
      </RowBlock>
      <RowBlock>
        <RowBlockTextContainer className="light">
          <h1>Text</h1>
          <p>
            Post-ironic live-edge vice raclette migas leggings marfa schlitz,
            twee shoreditch meggings. Austin whatever tbh gastropub mlkshk
            wayfarers four loko. Helvetica mustache blog irony artisan
            flexitarian pickled beard activated charcoal wayfarers tofu fixie.
            Distillery intelligentsia woke tilde meditation.
          </p>
          <p>
            Bicycle rights street art green juice, kale chips affogato gentrify
            distillery offal jianbing wolf wayfarers salvia af edison bulb.
            Brunch pok pok plaid, chartreuse butcher microdosing hell of
            affogato cardigan la croix bitters street art iPhone.{" "}
          </p>
        </RowBlockTextContainer>
      </RowBlock>
    </Row>
    <Row>
      <RowBlock>
        <RowBlockTextContainer>
          <h1>Text</h1>
          <p>
            Selfies yuccie poke taiyaki man bun live-edge adaptogen dreamcatcher
            keffiyeh crucifix fam squid lyft fanny pack. Banjo fashion axe
            glossier, put a bird on it skateboard tumeric taxidermy whatever
            chartreuse typewriter. IPhone master cleanse unicorn taxidermy. Swag
            tumblr actually glossier literally.
          </p>
        </RowBlockTextContainer>
      </RowBlock>
      <RowBlock>
        <ImageClipper className="ImageClipper">
          <BackgroundImage name="yellow_soap_hd.png"></BackgroundImage>
        </ImageClipper>
      </RowBlock>
    </Row>
  </Layout>
)

export default IndexPage
